.experience-container {
    max-width: 800px;
    margin: 40px auto;
    padding: 20px;
  }
  
  .experience-heading {
    font-size: 2em;
    color: var(--text-color);
    text-align: center;
    margin-bottom: 30px;
  }
  
  .experience-item {
    margin-bottom: 40px;
    border-bottom: 1px solid #ddd;
    padding-bottom: 20px;
  }
  
  .experience-company {
    font-size: 1.5em;
    color: var(--text-color);
    margin-bottom: 5px;
  }
  
  .experience-position {
    font-size: 1.1em;
    color: var(--text-color);
    margin-bottom: 15px;
  }
  
  .experience-list {
    font-size: 1em;
    color: var(--text-color);
    line-height: 1.6;
    padding-left: 20px;
  }
  