.home-container {
    text-align: center;
    margin-top: 50px;
  }
  
  .profile-img {
    border-radius: 50%;
    margin-bottom: 20px;
    width: 200px;
    height: 200px;
    object-fit: cover;
  }
  
  .home-heading {
    font-size: 2.5em;
    color: var(--text-color);
    margin-bottom: 10px;
  }
  
  .home-subheading {
    font-size: 1.2em;
    color: var(--text-color);
    max-width: 600px;
    margin: 0 auto;
  }
  